.header {
  text-align: center;
  background: #f1f1f1;
  color: orange;
  padding: 25px;
  margin: 0;
  font-family: "Arial Black";
}

.headerWrapper {
  font-size: xx-large;
}

.headerWrapper > h6 {
  color: #5a5a5a;
  font-weight: normal;
}

.mainCard {
  vertical-align: middle;
 height: 420px;
  border: lightgray solid;
  text-align: center;
  background: #e8f6f6;
  color: #5a5a5a;
  overflow: hidden;
}

.img {
  position: relative;
  top: 10px;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 150px;
  border: lightgray solid;
  border-radius: 20px;
  overflow: hidden;
  width: 85%;
}

.img > img{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 85%;
  max-width:100%;
  max-height:100%;
}

.item {
  margin: auto;
}

*:focus {
  outline: none;
}

.newCount {
  color: orange!important;
  font-size: large;
  font-style: italic;
}

.footer {
  height: fit-content;
  text-align: center;
  background: #f1f1f1;
  color: black;
  padding: 25px;
  margin: 0;
  border: 5px solid #e7e7e7;
  font-family: "Arial Black";
}

.arrow {
  font-size:xxx-large;
  text-align: right;
}

.buttonStyles  {
  background: orange;
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin: 3px;
  cursor: pointer;
  fontSize: 18;
  color: white;
  font-size:xx-large;
}

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: lightblue;
  border-radius: 15px;

}

::-webkit-scrollbar-thumb {
  border-radius: 15px;
  background: linear-gradient(180deg, #00c6fb, #005bea);
  height: 60px;
}